import util from '@/common/util.js'
import graceChecker from '@/common/graceChecker.js'
export default {
	data() {
		return {
			mail: '',
			code: '',
			captchaFlag: true,
			captchaName: "获取验证码",
			captchaTime: 60,
		};
	},
	onLoad(object) {
		this.mail = uni.getStorageSync('mail')
	},
	methods: {
		// 获取验证码
		getCaptcha() {
			let _this = this;
			var mailRule = [
				{
					name: 'mail',
					checkType: 'notnull',
					errorMsg: '请输入邮箱'
				},
				{
					name: 'mail',
					checkType: 'email',
					errorMsg: '邮箱格式错误'
				},

			];
			var checkRes = graceChecker.check(_this, mailRule);
			if (!checkRes) {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
				_this.isFocus = true;
				return;
			}
			uni.request({
				url: this.Server_IP + 'userIdfromMail', //仅为示例，并非真实接口地址。
				data: {
					mail: this.mail
				},
				header: {
					'custom-header': 'userIdfromMail' //自定义请求头信息
				},
				method:"POST",
				dataType:"json",
				success: (res) => {
					console.log(res.data);
					if(res.data.info.code == '0'){
						
						console.log(res.data.data.userId)
						console.log("成功")
						_this.captchaFlag = false;
						_this.captchaShow = true;
						var interval = setInterval(() => {--_this.captchaTime;}, 1000)
						setTimeout(() => {
							clearInterval(interval)
							_this.captchaFlag = true
							_this.captchaTime = 60
						}, 60000)
						console.log(this.mail)
						uni.request({
							url: this.Server_IP + 'mailcode', //仅为示例，并非真实接口地址。
							data: {
								mail: this.mail
							},
							header: {
								'custom-header': 'mailcode' //自定义请求头信息
							},
							method:"POST",
							dataType:"json",
							success: (res) => {
								console.log(res.data);
								if(res.data.info.code == '0'){
									
									console.log(res.data.data.userId)
									console.log("成功")
									uni.showToast({
										icon: 'none',
										title: "验证码发送成功，此邮件可能被归类到垃圾箱中，请注意"
									});
									
								}else{
									uni.showToast({
										icon: 'none',
										title: res.data.info.message
									});
								}
							},
						});
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.info.message
						});
						return
					}
				},
			});
			
			

			
		},
		next() {
			let _this = this
			var codeRule = [
				{
					name: 'code',
					checkType: 'notnull',
					errorMsg: '请输入验证码'
				},
				{
					name: 'code',
					checkType: 'string',
					checkRule: '6',
					errorMsg: '验证码为6个字符'
				}
			];
			var checkRes = graceChecker.check(_this, codeRule);
			if (!checkRes) {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
				_this.isFocus = true;
				return;
			}
			uni.request({
				url: this.Server_IP + 'checkCode', //仅为示例，并非真实接口地址。
				data: {
					mail: this.mail,
					code: this.code
				},
				header: {
					'custom-header': 'checkCode' //自定义请求头信息
				},
				method:"POST",
				dataType:"json",
				success: (res) => {
					console.log(res.data);
					if(res.data.info.code == '0'){
						console.log("验证码正确")
						uni.request({
							url: this.Server_IP + 'userIdfromMail', //仅为示例，并非真实接口地址。
							data: {
								mail: this.mail,
							},
							header: {
								'custom-header': 'userIdfromMail' //自定义请求头信息
							},
							method:"POST",
							dataType:"json",
							success: (res) => {
								console.log(res.data);
								if(res.data.info.code == '0'){
									console.log("验证码正确")
									uni.setStorageSync('userId', res.data.data.userId);
									uni.setStorageSync('isLogin', true);
									uni.switchTab({
										url: '/pages/index/index'
									});
								}else{
									uni.showToast({
										icon: 'none',
										title: res.data.info.message
									});
								}
							},
						});
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.info.message
						});
					}
				},
			});

		}
	}
}
